<template>
  <div class="keyboard">
    <div class="keyboard-row">
      <div style="font-size: 14px; text-align: center" :class="keyClasses()">
        {{ this.stenoEnabled ? "QWERTY MODE" : "STENO MODE" }}
      </div>
      <div v-for="[qwertyKey, stenoKey] in keysByRow[0]" :key="qwertyKey" :class="keyClasses(qwertyKey)">
        <div>{{ this.stenoEnabled ? stenoKey : qwertyKey }}</div>
        <div v-if="this.stenoEnabled && stenoKey" class="sublabel"> {{ qwertyKey }}</div>
      </div>
      <div class="modifier right" style="width: 118px" :class="keyClasses()">
        {{ this.stenoEnabled ? "delete word" : "delete" }}
      </div>
    </div>
    <div class="keyboard-row">
      <div class="modifier left" style="width: 118px" :class="keyClasses()">
        {{ this.stenoEnabled ? "" : "tab" }}
      </div>
      <div v-for="[qwertyKey, stenoKey] in keysByRow[1]" :key="qwertyKey" :class="keyClasses(qwertyKey)">
        <div>{{ this.stenoEnabled ? stenoKey : qwertyKey }}</div>
        <div v-if="this.stenoEnabled && stenoKey" class="sublabel"> {{ qwertyKey }}</div>
      </div>
    </div>
    <div class="keyboard-row">
      <div class="modifier left" style="width: 132px" :class="keyClasses()">
        {{ this.stenoEnabled ? "" : "caps lock" }}
      </div>
      <div v-for="[qwertyKey, stenoKey] in keysByRow[2]" :key="qwertyKey" :class="keyClasses(qwertyKey)">
        <div>{{ this.stenoEnabled ? stenoKey : qwertyKey }}</div>
        <div v-if="this.stenoEnabled && stenoKey" class="sublabel"> {{ qwertyKey }}</div>
      </div>
      <div class="modifier right" style="width: 132px" :class="keyClasses()">
        {{ this.stenoEnabled ? "" : "return" }}
      </div>
    </div>
    <div class="keyboard-row">
      <div class="modifier left" style="width: 170px" :class="keyClasses()">
        {{ this.stenoEnabled ? "" : "shift" }}
      </div>
      <div v-for="[qwertyKey, stenoKey] in keysByRow[3]" :key="qwertyKey" :class="keyClasses(qwertyKey)">
        <div>{{ this.stenoEnabled ? stenoKey : qwertyKey }}</div>
        <div v-if="this.stenoEnabled && stenoKey" class="sublabel"> {{ qwertyKey }}</div>
      </div>
      <div class="modifier right" style="width: 170px" :class="keyClasses()">
        {{ this.stenoEnabled ? "" : "shift" }}
      </div>
    </div>
  </div>
</template>

<style scoped>
.keyboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: fit-content;
  margin: 0 auto;
  padding: 20px;
  background-color: #e3e3e3;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.keyboard-row {
  display: flex;
  flex-direction: row;
  gap: 6px;
  margin-bottom: 6px;
  /* align-items: center; */
}

.key {
  width: 70px;
  height: 70px;
  background-color: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  cursor: default;
  user-select: none;
  padding: 10px;
  box-sizing: border-box;
  flex-direction: column;

  border: 1px solid #999999;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  transition: background-color 0.2s;
}

.key .sublabel {
  font-size: 16px;
  line-height: 1;
}

.key.modifier {
  font-size: 16px;
  align-items: flex-end;
  flex-direction: row;
}

.key.modifier.left {
  justify-content: left;
}

.key.modifier.right {
  justify-content: right;
}

.key.success {
  background-color: #87a5e0;
}

.key.failure {
  background-color: #e08787;
}

/* ensure .currentlyPressed is after .hasBeenPressed 
so currentlyPressed will take precedence */
.key.hasBeenPressed {
  background-color: rgb(120, 120, 120);
  color: #fff;
}

.key.currentlyPressed {
  background-color: rgb(32, 32, 32);
  color: #fff;
}
</style>

<script>
import { qwertyToSteno } from "@/engine";

// Each entry in 2d array at [row][column] is
// [<qwerty key>, <steno key if one exists, undefined otherwise>]
// 1-unit wide keys only, other keys have special CSS and are defined inline
// in the HTML template. Similarly, tilde (mode switch) needs special CSS
// and is defined inline in the HTML template.
// Special logic to ensure - is not displayed as a user-facing steno key
// even though it exists in the map.
const keysByRow = [
  ["1", "2", "3", "4", "5", "6", "7", "8", "9", "0", "-", "="],
  ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P", "[", "]", "\\"],
  ["A", "S", "D", "F", "G", "H", "J", "K", "L", ";", "'"],
  ["Z", "X", "C", "V", "B", "N", "M", ",", ".", "/"],
].map((row) =>
  row.map((key) => [key, key !== "-" ? qwertyToSteno.get(key) : undefined])
);

export default {
  name: "VirtualKeyboard",
  data() {
    return {
      colorClass: null,
      timeoutInFlight: null,
      keysByRow: keysByRow,
    };
  },
  props: {
    currentlyPressed: Set,
    hasBeenPressed: Set,
    stenoEnabled: Boolean,
  },
  methods: {
    getCurrentWord() {
      const flattenedKeys = keysByRow.flat();
      if (this.currentWordIndex >= 0 && this.currentWordIndex < flattenedKeys.length) {
        const key = flattenedKeys[this.currentWordIndex];
        return this.stenoEnabled ? key[1] : key[0];
      }
      // Return a default value or handle the out-of-bounds case as needed
      return "";
    },
    keyClasses(letter) {
      return [
        "key", // always .key
        this.colorClass, // .success or .failure as relevant
        {
          hasBeenPressed: this.hasBeenPressed.has(letter),
          currentlyPressed: this.currentlyPressed.has(letter),
        }, // .hasBeenPressed and .currentlyPressed as relevant
      ];
    },
    flashOnceOrReset(colorClass) {
      // if another flash is in flight, cancel it
      if (this.timeoutInFlight) {
        clearTimeout(this.timeoutInFlight);
      }
      // change the color class
      this.colorClass = colorClass;
      // if it's a non-null (non-default) color class, it's a flash
      // otherwise it's a reset that doesn't need a followup
      if (colorClass) {
        this.timeoutInFlight = setTimeout(() => (this.colorClass = null), 250);
      }
    },
    flashSuccess() {
      this.flashOnceOrReset("success");
    },
    flashFailure() {
      this.flashOnceOrReset("failure");
    },
    resetFlash() {
      this.flashOnceOrReset(null);
    },
  },
};
</script>