<template>
  <div class="practice-mode-container">
    <h2 class="head">Steno Practice Mode</h2>
    <div v-if="currentWord" class="word-container">
      <p class="current-word">{{ currentWord }}</p>
      <p class="hint">{{ currentHint }}</p>
      <StenoBox v-model="userInput" @correctInput="handleCorrectInput" />
    </div>
    <button @click="nextWord" class="next-word-btn">Next Word</button>
  </div>
</template>

<script>
import StenoBox from "./StenoBox.vue";

export default {
  components: {
    StenoBox,
  },
  data() {
    return {
      wordsToPractice: ["gauge", "reader", "attention",
        "the", "quick", "brown", "fox", "jumps", "over", "the", "lazy", "dog",
        "hello", "how are you?",
        "what", "is", "your", "favorite", "color",
        "I", "like", "ice", "cream",
        "practice", "makes", "perfect",
        "coding", "is", "a", "valuable", "skill",
        "the", "early", "bird", "catches", "the", "worm",
        "actions", "speak", "louder", "than", "words",
        "Rome", "wasn't", "built", "in", "a", "day"
      ],
      hintsToPractice: ["TKPWAEUPBLG", "RAOERD", "TAEPBGS",
        "T-T", "KWEUPL", "PWROUPB", "TPOBGS", "SKWR*UPLS", "A*UFR", "T-T", "HRAEU/S*EU", "TKOG",
        "HO*EL", "HOU/R/STPHU",
        "KWHA", "S", "SKWROUR", "TPA*EUF/REUT", "KOL/O*R",
        "EU", "PHRAOEUBG", "AOEUS", "KRAOEPL",
        "PHRABG/TEUS/-S", "PHAEUB/-S", "P-FRT",
        "KOED/-G", "S", "AEU", "SRAEBL", "SKEUL",
        "T-T", "*ER/HREU", "PWEURD", "KAFPS", "T-T", "WORPL",
        "-BGSZ", "SPAOEBG", "HROURD", "THAP", "WORBDZ",
        "RO*EPL", "WAEPBT", "PWEULT", "TPH", "AEU", "TKAEU"
      ],
      currentWordIndex: 0,
      userInput: "",
    };
  },
  computed: {
    currentWord() {
      return this.wordsToPractice[this.currentWordIndex];
    },
    currentHint() {
      return this.hintsToPractice[this.currentWordIndex];
    },
  },
  methods: {
    nextWord() {
      this.currentWordIndex = (this.currentWordIndex + 1) % this.wordsToPractice.length;
      this.userInput = "";
    },
    handleCorrectInput() {
      const currentWordLowerCase = this.currentWord.toLowerCase();
      const typedWordLowerCase = this.userInput.trim().toLowerCase();

      if (currentWordLowerCase === typedWordLowerCase) {
        this.nextWord();
      }
    },
  },
};
</script>

