<template>
    <div>
        <!-- Main Text Box -->
        <textarea ref="textbox" :value="modelValue" @input="$emit('update:modelValue', $event.target.value)" type="text"
            style="
          width: 100%;
          height: 100px;
          font-size: 16px;
          padding: 10px;
          box-sizing: border-box;
        " @keydown="handleKeyDown" @keyup="handleKeyUp"></textarea>

        <!-- On-Screen Keyboard Display -->
        <Keyboard :currentlyPressed="engine.currentlyPressed" :hasBeenPressed="engine.hasBeenPressed"
            :stenoEnabled="stenoEnabled" ref="keyboard" @keydown="handleKeyDown" @keyup="handleKeyUp" />
    </div>
</template>
  
<script>
import Keyboard from "./VirtualKeyboard.vue";
import Engine from "@/engine";

export default {
    name: "StenoBox",

    components: {
        Keyboard,
    },

    props: {
        modelValue: String,
    },

    emits: ["update:modelValue", "correctInput"],

    computed: {
        destination: {
            get() {
                return this.$props.modelValue;
            },
            set(value) {
                this.$emit("update:modelValue", value);
            },
        },
    },

    data() {
        return {
            stenoEnabled: true,
            engine: new Engine(),
            wordsToPractice: ["gauge", "reader", "attention"],
            currentWordIndex: 0,
        };
    },

    // focus text box on page load
    mounted() {
        this.focusTextbox();
    },

    methods: {
        focusTextbox() {
            this.$refs.textbox.focus();
        },

        handleKeyDown(event) {
            const qwertyKey = event.key.toUpperCase();

            // special handling: mode switching works in both modes
            if (qwertyKey === "`" && !event.repeat) {
                this.stenoEnabled = !this.stenoEnabled;
                event.preventDefault();
                return;
            }

            // don't do any more processing unless we're in steno mode
            if (!this.stenoEnabled) {
                return;
            }

            this.$refs.keyboard.resetFlash();
            event.preventDefault();

            // special handling: delete last word if backspace is pressed
            if (qwertyKey === "BACKSPACE") {
                // handle backspace at key-down time so hold to repeat works
                let indexOfLastSpace = this.destination.lastIndexOf(" ");
                // special case: if no spaces are present, delete the last word
                // by deleting everything
                if (indexOfLastSpace === -1) {
                    indexOfLastSpace = 0;
                }
                this.destination = this.destination.slice(0, indexOfLastSpace);
                this.engine.clearMemory();
                return;
            }

            this.engine.keyDown(qwertyKey);
        },
        handleKeyUp(event) {
            // don't do any processing unless we're in steno mode
            if (!this.stenoEnabled) {
                return;
            }

            const qwertyKey = event.key.toUpperCase();
            event.preventDefault();
            this.$refs.keyboard.resetFlash();

            const maybeResult = this.engine.keyUp(qwertyKey, this.destination);
            if (!maybeResult) {
                // stroke not over yet
                return;
            }

            if (maybeResult.success) {
                this.destination = maybeResult.buffer;
                this.$refs.keyboard.flashSuccess();

                // Check if the typed word matches the current word
                this.checkMatchingWord();
                // Notify the parent component that the correct word is typed
                this.$emit("correctInput");
            } else {
                this.$refs.keyboard.flashFailure();
            }
        },

        checkMatchingWord() {
            const currentWordLowerCase = this.wordsToPractice[this.currentWordIndex].toLowerCase();
            const typedWordLowerCase = this.modelValue.toLowerCase();

            console.log('Current Word:', currentWordLowerCase);
            console.log('Typed Word:', typedWordLowerCase);

            // Check if the typed word matches the current word (case-insensitive)
            if (currentWordLowerCase === typedWordLowerCase) {
                // Move to the next word when the typed word matches
                console.log('Match! Moving to the next word.');
                this.nextWord();
            } else if (currentWordLowerCase.startsWith(typedWordLowerCase)) {
                console.log('Partial match. Keep typing.');
            } else {
                console.log('No match.');
            }
        },

        nextWord() {
            this.currentWordIndex = (this.currentWordIndex + 1) % this.wordsToPractice.length;
        },
    },
};
</script>

