<!-- Header.vue -->
<template>
  <header>
    <div id="nameDiv">
      <h1 id="headerName">{{ websiteName }}</h1>
      <div class="buttons">
        <button v-for="mode in modes" :key="mode.id" @click="changeMode(mode.name)"
          :class="{ active: selectedMode === mode.name }">
          {{ mode.name }}
        </button>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "headerComp",
  props: {},

  data() {
    return {
      websiteName: "Steno Web Tool",
      selectedMode: "Home", // Default selected mode
      modes: [
        { id: 1, name: "Home" },
        { id: 2, name: "Practice" },
        { id: 3, name: "Freestyle" },
      ],
    };
  },

  methods: {

    // Method responsible for sending sending mode to the parent
    changeMode(mode) {
      this.selectedMode = mode;
      this.$emit('modeChange', mode);
    },
  },
};
</script>

