import defaultDictionary from "@/plover_main.json";

// Keys are in canonical order - do not reorder!
//
// We bind steno key Z to both : and ; to provide invariance with
// respect to the user holding SHIFT. This invariance is achieved
// for other keys by uppercasing the key events using String.toUpperCase(),
// but ":".toUpperCase() === ":" !== ";" so we need two entries for this key.
//
// Within a single stroke in a dictionary entry, a dash `-` may or may not
// separate the left-side keys from the right-side keys. This seems to be
// an organizational choice on the part of the dictionary designer.
// We want to insert a dash between left and right side keys, in case it is
// present in the dictionary form for the stroke. It may not be, but it's
// easier to insert it now and remove it in the lookup stage if an entry with
// a dash is not found, rather than trying to compute the placement of the dash
// later.
// Empirically, the dash never interacts with vowels or asterisks,
// so it's safe to insert immediately after the left-side keys.
export const qwertyToSteno = new Map([
    ["Q", "S"],
    ["A", "S"],
    ["W", "T"],
    ["S", "K"],
    ["E", "P"],
    ["D", "W"],
    ["R", "H"],
    ["F", "R"],
    ["-", "-"], // sentinel - insert dash at this point in the order
    ["C", "A"],
    ["V", "O"],
    ["T", "*"],
    ["G", "*"],
    ["N", "E"],
    ["M", "U"],
    ["Y", "F"],
    ["H", "R"],
    ["U", "P"],
    ["J", "B"],
    ["I", "L"],
    ["K", "G"],
    ["O", "T"],
    ["L", "S"],
    ["P", "D"],
    [":", "Z"],
    [";", "Z"],
]);
function qwertyKeyDoesMapToSteno(qwertyKey) {
    return qwertyToSteno.has(qwertyKey);
}

// Convenience association from QWERTY keys to their indices in
// the canonical order, to make sorting easy
const qwertyToCanonicalIndex = new Map(
    Array.from(qwertyToSteno.keys()).map((qwertyKey, index) => [qwertyKey, index])
);

// Steno keys S and * are each bound to two QWERTY keys simultaneously.
// Note this is distinct from the case where multiple steno keys exist
// that are labeled with the same letter (such as left-T and right-T).
//
// Algorithmically, we need to ensure that if both QWERTY keys for a single
// steno key appear, we only map the corresponding steno key once.
//
// Also, because of the special handling of steno key Z to maintain
// SHIFT-invariance, we need to avoid mapping it twice if the
// user presses ; and SHIFT + ; = : in the same stroke.
const multiBoundKeys = [
    ["Q", "A"],
    ["T", "G"],
    [":", ";"],
];

function dedupMultiBoundKeysInPlace(QWERTYKeyArray) {
    for (const multiBoundKeyGroup of multiBoundKeys) {
        let groupPresent = false;
        for (const multiBoundKey of multiBoundKeyGroup) {
            const idx = QWERTYKeyArray.indexOf(multiBoundKey);
            if (idx !== -1) {
                if (groupPresent) {
                    QWERTYKeyArray.splice(idx, 1);
                } else {
                    groupPresent = true;
                }
            }
        }
    }
}

function canonicalize(QWERTYKeySet) {
    // Convert to array so we can sort
    // and so we can modify the contents without affecting the caller's version
    const QWERTYKeyArray = Array.from(QWERTYKeySet);

    // Ensure we didn't get any keys we don't know how to process.
    if (!QWERTYKeyArray.every((key) => qwertyToSteno.has(key))) {
        throw new Error(
            `canonicalization failed: invalid keys in "${QWERTYKeyArray.join("")}"`
        );
    }

    // Set mostly achieves deduplication, but special handling is required
    // for multi-bound keys (see comment on declaration of multiBoundKeys).
    dedupMultiBoundKeysInPlace(QWERTYKeyArray);

    // Once fully deduplicated, we need to sort the keys into canonical order.
    //
    // To ensure a dash is present, we can just append it to the QWERTY array,
    // and it will get mapped to a dash and sorted into the correct position
    // due to the sentinel value in the mapping.
    QWERTYKeyArray.push("-");

    // Put the keys in canonical order and map to steno
    const canonicalStenoArray = QWERTYKeyArray.sort(
        (e1, e2) => qwertyToCanonicalIndex.get(e1) - qwertyToCanonicalIndex.get(e2)
    ).map((e) => qwertyToSteno.get(e));

    // Return the dictionary entry in string form
    return canonicalStenoArray.join("");
}

function joinMemory(memory, startIndex) {
    return memory
        .slice(startIndex)
        .map(({ chord }) => chord)
        .join("/");
}

export default class Engine {
    constructor(dictionary = defaultDictionary) {
        this.dictionary = dictionary;
        this.currentlyPressed = new Set();
        this.hasBeenPressed = new Set();
        this.memory = [];
    }

    keyDown(qwertyKey) {
        const qwertyKeyUpper = qwertyKey.toUpperCase();
        // ignore non-steno keys
        if (!qwertyKeyDoesMapToSteno(qwertyKeyUpper)) {
            return;
        }
        this.currentlyPressed.add(qwertyKeyUpper);
        this.hasBeenPressed.add(qwertyKeyUpper);
    }

    dictionaryLookup(stenoKeyString) {
        return this.dictionary[stenoKeyString];
    }

    dictionaryHasPrefix(stenoPrefix) {
        return Object.keys(this.dictionary).some((stroke) =>
            stroke.startsWith(stenoPrefix)
        );
    }

    clearMemory() {
        this.memory.splice(0);
    }

    keyUp(qwertyKey, oldBuffer) {
        const qwertyKeyUpper = qwertyKey.toUpperCase();
        // ignore non-steno keys
        if (!qwertyKeyDoesMapToSteno(qwertyKeyUpper)) {
            return;
        }
        this.currentlyPressed.delete(qwertyKeyUpper);

        if (this.currentlyPressed.size != 0) {
            // chord not over yet
            return;
        }

        // execute
        const currentChord = canonicalize(this.hasBeenPressed);
        this.hasBeenPressed.clear();
        const currentMemCell = {
            chord: currentChord,
            cutPoint: oldBuffer.length,
        };

        // find the most complex stroke we can currently execute
        let currentCellIdx = this.memory.length + 1;
        let currentStrokeResult;
        for (let i = 0; i < this.memory.length + 1; i++) {
            // the current chord may be represented in the dictionary
            // either with or without a dash. Try looking it up with the dash
            // first, then try without the dash if that fails.
            // Ensure the memory entry contains the correct form so we
            // don't need to do a combinatorial number of lookups for
            // longer strokes.
            currentStrokeResult = this.dictionaryLookup(
                joinMemory([...this.memory, currentMemCell], i)
            );
            if (currentStrokeResult === undefined) {
                currentMemCell.chord = currentMemCell.chord.replace(/-/g, "");
                currentStrokeResult = this.dictionaryLookup(
                    joinMemory([...this.memory, currentMemCell], i)
                );
            }
            if (currentStrokeResult !== undefined) {
                currentCellIdx = i;
                break;
            }
        }
        this.memory.push(currentMemCell);
        // invariants once this line is reached:
        // - currentCellIdx will point to the earliest cell such that
        // that cell and all subsequent cells join to form a legal stroke,
        // or the most recent cell if no legal stroke is found
        // (meaning the current chord is either incorrect, or it is
        // an early chord in a complex stroke that does not resolve on its own)
        // - currentStrokeResult will contain the result of the stroke made up
        // of cells starting at currentCellIdx, or undefined if no legal stroke
        // is found as described above

        // string copy
        let buffer = `${oldBuffer}`;
        // if the stroke thus far has a result, splice it into the buffer
        if (currentStrokeResult !== undefined) {
            // cut the buffer back to the cut point of the current memory cell
            // then add the new stroke result
            buffer = oldBuffer.slice(0, this.memory[currentCellIdx].cutPoint);
            if (buffer.length > 0 && !buffer.endsWith(" ")) {
                buffer += " ";
            }
            buffer += currentStrokeResult;
        }

        // prune the memory starting from the oldest entry
        // keep the longest prefix we can that actually exists in the dictionary
        //
        // NB if the current chord is an early chord in a complex stroke
        // that does not resolve on its own as described above,
        // after this loop the chord will remain in memory and `success: true`
        // will be returned to indicate the chord was recognized as legal
        // even though the buffer has not been updated
        for (let j = 0; j <= currentCellIdx; j++) {
            if (this.dictionaryHasPrefix(joinMemory(this.memory, j) + "/")) {
                this.memory.splice(0, j);
                return { success: true, buffer: buffer };
            }
        }
        // If we fall through to this point, either a stroke has definitively
        // completed (no possibilities to extend it, so clear memory and suceed), 
        // or the current chord is illegal (forget it and fail, but keep earlier
        // memory in case the user wants to keep using it)
        if (currentStrokeResult !== undefined) {
            // executed something
            this.clearMemory();
            return { success: true, buffer: buffer };
        }
        this.memory.pop();
        return { success: false, buffer: buffer };
    }
}
